import { distance } from './distance';

export const calculateDistances = function (target: any, macros: any) {
    // Where 1 = 100%
    const dists = {
        C: 1,
        P: 1,
        F: 1
    }
    dists.C = distance('C', target, macros);
    dists.P = distance('P', target, macros);
    dists.F = distance('F', target, macros);


    return dists;
}