import { MAX, TARGET, RECIPE } from './calculator/constants';
import { organizeRecipe } from './calculator/organize-recipe';
import { calculateCurrentMacros } from './calculator/calculate-current-macros';
import { calculateDistances } from './calculator/calculate-distances';
import { updateRecipeMacros } from './calculator/update-recipe-macros';

export const calculator = function (recipe: any = RECIPE, target: any = TARGET, i: number = 0) {

    recipe = organizeRecipe(recipe); // recipe._quantities.[type,ingredient,quantity]

    if (i > MAX) return recipe;

    const macros = calculateCurrentMacros(recipe);
    const dists = calculateDistances(target, macros);


    updateRecipeMacros(recipe, dists);

    i++;
    return calculator(recipe, target, i);
}


