export const defineRecipeIngredientsCategory = function (recipe: any) {
    recipe._quantities.map(quantity => {
        const ingredient = quantity.ingredient;
        const C = ingredient.carbs * 4;
        const P = ingredient.proteins * 4;
        const F = ingredient.fats * 9;

        if (F > P && F > C) {
            quantity.type = 'F';
        } else if (P > C && P > F) {
            quantity.type = 'P';
        } else {
            quantity.type = 'C'
        }

        return quantity;
    });

    return recipe;
}